:root {
  --dark: #362944;
	--red: #c45d9f;
	--orange: #e39aac;
	--yellow: #f0dab1;
	--blue: #6461c2;
	--cyan: #2ba9b4;
	--green: #93d4b5;
	--white: #f0f6e8;
}

html,
body {
	height: 100%;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
	background-color: var(--white);
  color: var(--black);
}

#d3ne {
	height: 100%;
	position: relative;
}

.node.title,
.node.task {
	color: var(--white);
}

.node.task {
	background-color: var(--blue);
	border: none;
}

.node.task:hover {
	background-color: var(--cyan);
}

.node.task.selected {
	background-color: var(--cyan);
}

.socket.input.action,
.socket.input.action.multiple,
.socket.output.action {
	background:  var(--yellow);
	border-color: var(--green);
}

.socket.act {
	background:  #93d4b5;
	border-radius: 0 !important;
	width: 16px !important;
}

id {
	position: absolute;
	left: -6px;
	top: -6px;
	background: white;
}

button.compile {
	z-index: 5;
	position: absolute;
	top: 10px;
	left: 10px;
}
